import React, { useEffect } from "react";
import LazyLoad from "react-lazyload";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  Button,
  Container,
  Grid,
  Image,
  Segment,
  Statistic,
} from "semantic-ui-react";
import CoinChart from "../../components/CoinChart";
import { _getFundList } from "../../redux/walletReducer";
import { formatAmount, formatMoney, formatTotal } from "../../settings/format";

function Dashboard() {
  const history = useHistory();
  const { wallet, setting } = useSelector((state) => state);
  const { library } = setting;
  const { fundList } = wallet;
  // const dispatch = useDispatch();

  // useEffect(() => {
  //   dispatch({ type: "FETCH_FUND_LIST", payload: [] });
  //   dispatch(_getFundList());
  // }, [dispatch]);

  let sum = fundList.reduce((a, b) => {
    return a + b.usdAmount;
  }, 0);

  return (
    <Container textAlign="center" className="dashboard">
      <LazyLoad>
        <div style={{ position: "relative" }}>
          <CoinChart />
          <Statistic
            size="small"
            inverted
            style={{
              position: "absolute",
              bottom: 0,
              top: 0,
              right: "50%",
              left: "50%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              margin: 0,
            }}
          >
            <Statistic.Label style={{ color: "var(--green)" }}>
              {library.TOTAL}
            </Statistic.Label>
            <Statistic.Value>
              <span style={{ fontSize: 25 }}>$</span>
              <span style={{ fontSize: 35 }}>{formatTotal(sum)}</span>
            </Statistic.Value>
          </Statistic>
        </div>
      </LazyLoad>
      <LazyLoad>
        <Segment className="balances" padded>
          <Grid columns="equal" textAlign="right">
            <Grid.Row only="computer" style={{ fontWeight: 600 }}>
              <Grid.Column textAlign="left" width={4}>
                {library.COIN_TOKEN}
              </Grid.Column>
              <Grid.Column>{library.PRICE}</Grid.Column>
              <Grid.Column>{library.BALANCE}</Grid.Column>
              <Grid.Column>{library.VALUE}</Grid.Column>
              <Grid.Column></Grid.Column>
              <Grid.Column></Grid.Column>
            </Grid.Row>
            {wallet.fundList.map((w, index) => (
              <React.Fragment key={index}>
                <Grid.Row only="computer">
                  <Grid.Column
                    style={{ display: "flex" }}
                    textAlign="left"
                    width={4}
                  >
                    <Image
                      src={`/images/${w.icon.toLowerCase()}`}
                      verticalAlign="middle"
                      className="coin-image"
                    />
                    <div style={{ marginLeft: 20 }}>
                      <div style={{ fontWeight: 600, fontSize: 15 }}>
                        {w.fullName}
                      </div>
                      <div>{w.coin}</div>
                    </div>
                  </Grid.Column>
                  <Grid.Column verticalAlign="middle">
                    ${formatMoney(w.usdPrice)}
                  </Grid.Column>
                  <Grid.Column
                    verticalAlign="middle"
                    style={{ color: w.color, fontWeight: 600 }}
                  >
                    {formatAmount(w.amount)}
                  </Grid.Column>
                  <Grid.Column
                    verticalAlign="middle"
                    style={{ color: w.color, fontWeight: 600 }}
                  >
                    ${formatMoney(w.usdAmount)}
                  </Grid.Column>
                  <Grid.Column textAlign="right">
                    <Button
                      onClick={() => history.push("/deposit/" + w.coin)}
                      className="spwallet-button"
                      disabled={!w.depositEnable}
                    >
                      {library.DEPOSIT}
                    </Button>
                  </Grid.Column>
                  <Grid.Column textAlign="left">
                    <Button
                      onClick={() => history.push("/withdraw/" + w.coin)}
                      basic
                      inverted
                      disabled={!w.withdrawEnable}
                    >
                      {library.WITHDRAW}
                    </Button>
                  </Grid.Column>
                </Grid.Row>
                <Grid.Row only="mobile tablet">
                  <Grid.Column
                    textAlign="left"
                    style={{ display: "flex", justifyContent: "space-between" }}
                  >
                    <Grid.Column style={{ display: "flex" }} width={8}>
                      <Image
                        src={`/images/${w.icon.toLowerCase()}`}
                        verticalAlign="middle"
                        className="coin-image"
                      />
                      <div style={{ marginLeft: 20 }}>
                        <div>{w.fullName}</div>
                        <div>${formatMoney(w.usdPrice)}</div>
                        {/* <div>{w.coin}</div> */}
                      </div>
                    </Grid.Column>
                    <Grid.Column width={8}>
                      <div
                        style={{
                          textAlign: "right",
                          color: w.color,
                          fontWeight: 600,
                        }}
                      >
                        {formatAmount(w.amount)} {w.coin}
                      </div>
                      <div
                        style={{
                          textAlign: "right",
                          color: w.color,
                          fontWeight: 600,
                        }}
                      >
                        ${formatMoney(w.usdAmount)}
                      </div>
                    </Grid.Column>
                  </Grid.Column>
                  <Grid.Column
                    width={16}
                    textAlign="center"
                    style={{ marginTop: "1em" }}
                  >
                    <Button
                      onClick={() => history.push("/deposit/" + w.coin)}
                      className="spwallet-button"
                      disabled={!w.depositEnable}
                    >
                      {library.DEPOSIT}
                    </Button>
                    <Button
                      onClick={() => history.push("/withdraw/" + w.coin)}
                      basic
                      inverted
                      disabled={!w.withdrawEnable}
                    >
                      {library.WITHDRAW}
                    </Button>
                  </Grid.Column>
                </Grid.Row>
              </React.Fragment>
            ))}
          </Grid>
        </Segment>
      </LazyLoad>
      {/* <Segment>
        <Grid>
          <Grid.Column computer={8} mobile={16}>
            <Image src="images/sp-wallet-1.png" />
          </Grid.Column>
          <Grid.Column computer={8} mobile={16}>
            <Image src="images/sp-wallet-2.png" />
          </Grid.Column>
        </Grid>
      </Segment> */}
    </Container>
  );
}

export default Dashboard;
