import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Header, Icon, Image, Popup, Segment } from "semantic-ui-react";
import { get } from "../../utils/api";

function DepositForm({ coin, network }) {
  const [address, setAddress] = useState(null);
  const [loading, setLoading] = useState(true);
  const { setting } = useSelector((state) => state);
  const { library } = setting;
  const { COIN_TOKEN, NETWORK } = library;
  const [depositEnable, setDepositEnable] = useState(true);

  useEffect(() => {
    setLoading(true);
    get(
      `/fund-service/address?coin=${coin}&network=${network}`,
      (data) => {
        setDepositEnable(true);
        setLoading(false);
        setAddress(data.address);
      },
      (data) => {
        if (data.code === "DEPOSIT_DISABLE") {
          setDepositEnable(false);
        }
      }
    );
  }, [coin, network]);

  if (depositEnable) {
    return (
      <Segment loading={loading} vertical>
        <Header as="h5">
          {COIN_TOKEN}: {coin} - {NETWORK}: {network}
        </Header>
        <Image
          src={
            "https://chart.googleapis.com/chart?chs=500x500&cht=qr&chl=" +
            address
          }
          size="small"
          className="Logo"
          centered
        />
        <br />
        <Popup
          content="Copied"
          on="click"
          basic
          position="bottom center"
          hideOnScroll
          trigger={
            <p
              style={{
                textOverflow: "ellipsis",
                overflow: "hidden",
                cursor: "pointer",
              }}
              onClick={() => navigator.clipboard.writeText(address)}
            >
              {address}
              <Icon name="copy outline" size="large" />
            </p>
          }
        />
      </Segment>
    );
  } else {
    return <Segment basic>{library.DEPOSIT_DISABLE}</Segment>;
  }
}

export default DepositForm;
