import React, { useState } from "react";
import PropTypes from "prop-types";
import { Container } from "@material-ui/core";
import logo from "../../assets/images/logo-SPwallet.png";
import { useHistory } from "react-router-dom";

Header.propTypes = {};

function Header(props) {
  const [color, setColor] = useState("red");
  const history = useHistory();
  return (
    <Container maxWidth={"md"} className="p-0 color-primary">
      <div className="d-flex align-items-center justify-content-between height-40 font-size-09">
        <img
          src={logo}
          className="h-100"
          onClick={() => window.location.replace("/")}
          alt="spwallet-logo"
        />
        <span className="btn rounded-pill pt-1 pb-1 pl-3 pr-3 color-primary font-size-09 text-nowrap btn-hover">
          Các tài sản
        </span>
        <span className="btn rounded-pill pt-1 pb-1 pl-3 pr-3 color-primary font-size-09 text-nowrap btn-hover">
          Chuyển đổi
        </span>
        <span className="btn rounded-pill pt-1 pb-1 pl-3 pr-3 color-primary font-size-09 text-nowrap btn-hover">
          Stanking
        </span>
        <span className="btn rounded-pill pt-1 pb-1 pl-3 pr-3 color-primary font-size-09 text-nowrap btn-hover">
          Kiếm tiền
        </span>
        <span className="btn rounded-pill pt-1 pb-1 pl-3 pr-3 color-primary font-size-09 text-nowrap btn-hover">
          P2P
        </span>
        <div>
          <span
            className="btn border rounded-pill pt-1 pb-1 pl-4 pr-4 mr-2 font-size-09 border-primary border-width-1 color-primary btn-hover"
            onClick={() => history.push("/register")}
          >
            Đăng ký
          </span>
          <span
            className="btn background-primary rounded-pill pt-1 pb-1 pl-4 pr-4 mr-2 font-size-09 text-light btn-hover"
            onClick={() => history.push("/login")}
          >
            Đăng nhập
          </span>
          <span>Tiếng Việt</span>
        </div>
      </div>
    </Container>
  );
}

export default Header;
