import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

import BTCIcon from "../../assets/images/Bitcoin-BTC-icon.png";
import ETHIcon from "../../assets/images/eth.png";
import USDTIcon from "../../assets/images/usdt.png";
import SPCIcon from "../../assets/images/SPEx(100x100).png";
import mUSDIcon from "../../assets/images/MUSD100x100.png";
import { Grid } from "@material-ui/core";
import { get } from "../../utils/api";
import { formatAmount, formatMoney, formatTotal } from "../../settings/format";
import { useSelector } from "react-redux";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(icon, name, coin, price, change24h, volume) {
  return { icon, name, coin, price, change24h, volume };
}

